const reducer = (state = {priorityNumbers:[]}, action) => {
    switch (action.type) {
       case 'ADD': {
           let newArray = state.priorityNumbers.slice();
           let newCount = (newArray.length===0?1:newArray[newArray.length-1].count+1);
           newArray.splice(newArray.length, 0, 
            {
                count:newCount, 
                status: 1, 
                afterStatus: 2, 
                name: action.payload.name===''? ('Person ' + newCount): action.payload.name, 
                purpose: action.payload.purpose===''? ('Private Transaction') : action.payload.purpose
            });

           return {priorityNumbers:newArray};
       };
       case 'REMOVE': {
            let newArray = state.priorityNumbers.slice();
            newArray.splice(action.payload, 1);
            return {priorityNumbers:newArray};
       };
       default: return state;
    }
 }
 export default reducer;