import '../scss/board.scss';

import React, { Component } from 'react';
import AddItemComponent from './addItemComponent';

class BoardComponent extends Component {
    maxTimer = 10000;
    interval = 0;
    timers = [];
    constructor(){
        super();
        this.countdown = this.countdown.bind(this);
        this.showTimeRemaining = this.showTimeRemaining.bind(this);
    }
    componentWillReceiveProps(nextProps){
        if((nextProps.priorityNumbers.length === 1) || (nextProps.priorityNumbers.length < this.props.priorityNumbers.length && nextProps.priorityNumbers.length > 0)){
            this.countdown();
        }
    }
    showTimeRemaining(){
        let c = this.maxTimer/1000;
        return setInterval(() => {
            this.refs.timeRemaining.innerHTML = c-1;
            this.refs.percentRemaining.style.width = (110-parseInt((c/(this.maxTimer/1000))*100)) + '%';
            c--;
        }, 1000);
    }
    countdown(){
        const {remove} = this.props;
        setTimeout(() => {
            clearInterval(intervalId);
            if(this.refs.timeRemaining.innerHTML === '1'){
                this.refs.timeRemaining.innerHTML = '10';
                this.refs.percentRemaining.style.width = '100%';
            }
            remove(0);
        }, this.maxTimer);
        let intervalId = this.showTimeRemaining();
    }
   render() {
      const {priorityNumbers,add} = this.props;

      let nums = [];
      let limit = priorityNumbers.length;
      for (let i = 0; i < limit; i++) {
        nums.push(
            <div key={i} className={"board-item " + (i===0?'serving': (i+1===limit?'reserved':'reserved'))}>
                <div className="item-number">{priorityNumbers[i].count}</div>
                <div className="item-status reserved">
                    {i===0?'Serving ' + priorityNumbers[i].name : 'Reserved by ' + priorityNumbers[i].name}<br></br>
                    <span className="purpose">{priorityNumbers[i].purpose}</span>
                    {
                        i===0?
                        <div className="time-remaining"><span ref="timeRemaining">{this.maxTimer/1000}</span></div>
                        :''
                    }
                    {
                        i===0?
                        <div className="percent-remaining" ref="percentRemaining">
                        </div>
                        :''
                    }  
                </div>
              
           </div>     
          );
      } 
      return (
         <div className="board-container">
            {nums}
            <div className={'board-item'}>
                <AddItemComponent availableNum={nums.length>0?(priorityNumbers[nums.length-1].count+1) : nums.length+1} addCallback={add}></AddItemComponent>
            </div>                 
         </div>
      );
   }
}
export default BoardComponent; 