import { connect } from 'react-redux';
import BoardComponent from '../components/boardComponent';
import { add, remove } from '../actions/action';
//this file is used so that the component can access the redux state and props
const mapStateToProps = (state) => {
   return {
      priorityNumbers: state.priorityNumbers
   };
};
const mapDispatchToProps = (dispatch) => {
   return {
      add: (item) => dispatch(add(item)),
      remove: (count) => dispatch(remove(count))
   };
};
export default connect(mapStateToProps, mapDispatchToProps)(BoardComponent);