import '../scss/home.scss';
import '../scss/additem.scss';

import React, { Component } from 'react';

class AddItemComponent extends Component {
    constructor() {
        super();
        this.addItem = this.addItem.bind(this);
      }
    addItem() {
        return this.props.addCallback({name:this.refs.itemName.value, purpose:this.refs.itemPurpose.value});
    }
    render() {
        return(
            <div className="add-item-container">
            <div className="number-container">
                <span>{this.props.availableNum}</span>
            </div>
            <div className="info-container">
                <input ref="itemName" type="text" placeholder="Name"></input>
                <input ref="itemPurpose" type="text" placeholder="Purpose"></input>
            </div>
            <div className="button-container">
                <input onClick={this.addItem} type="button" value="+ Add"></input>
            </div>
            </div>
        );
    }
 }
 export default AddItemComponent; 